import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { URLS } from '@enums/urls';
import { AppStateInterface } from '@store/app/app-state.interface';
import { Store } from '@ngrx/store';
import { selectCurrentUserProfileName } from '@store/auth/auth.selectors';
import { Observable } from 'rxjs';
import { DATE_FORMAT_ARTWORK } from '@enums/date-format';
import { MessageService } from 'primeng/api';
import { TranslateService } from '@ngx-translate/core';

interface DateFormatOption {
  name: string;
  code: DATE_FORMAT_ARTWORK;
}

@Component({
  selector: 'app-new-artwork',
  templateUrl: 'new-artwork.component.html',
  styleUrls: ['new-artwork.component.scss'],
})
export class NewArtworkComponent implements OnInit {
  @ViewChild('fileInput') fileInput!: ElementRef;
  maxDate!: Date;
  isDragover: boolean = false;
  aspectRatio: number = 1;
  showAspectHorizontalButtons: boolean = false;
  showAspectVerticalButtons: boolean = false;
  fileToCropper: File | null = null;
  croppedFile: string | null = null;
  maxLengthDescription: number = 5000;
  userName$: Observable<string | null> = this.store.select(selectCurrentUserProfileName);
  currentDateFormat: DATE_FORMAT_ARTWORK = DATE_FORMAT_ARTWORK.FULL_DATE;
  dateFormatOptions: DateFormatOption[] = [];
  maintainAspectRatio: boolean = true;

  constructor(
    private store: Store<AppStateInterface>,
    private fb: FormBuilder,
    private toast: MessageService,
    private translate: TranslateService,
  ) {}

  newArtworkForm = this.fb.group({
    artists: this.fb.array([this.fb.control('')]),
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required]),
    additional: new FormGroup({
      technique: new FormControl(''),
      dimensions: new FormControl(''),
      material: new FormControl(''),
      preservation: new FormControl(''),
      location: new FormControl(''),
      others: new FormControl(''),
    }),
    date: new FormGroup({
      year: new FormControl(''),
      monthYear: new FormControl(''),
      full: new FormControl(''),
      century: new FormControl('', [Validators.pattern(/^M{0,4}(CM|CD|D?C{0,3})(XC|XL|L?X{0,3})(IX|IV|V?I{0,3})$/)]),
      decade: new FormControl('', [Validators.pattern(/^(\d{0,3}0)$/)]),
      afterChrist: new FormControl(false),
      other: new FormControl(''),
    }),
    isArtistUser: new FormControl(false, [Validators.required]),
    agreeToTerms: new FormControl(false, [Validators.required]),
    price: new FormControl(0),
  });

  get artists() {
    return this.newArtworkForm.controls['artists'] as FormArray;
  }

  get date() {
    return this.newArtworkForm.controls['date'] as FormGroup;
  }

  get additional() {
    return this.newArtworkForm.controls['additional'] as FormGroup;
  }

  ngOnInit() {
    this.maxDate = new Date();

    this.dateFormatOptions = [
      {
        name: 'Century',
        code: DATE_FORMAT_ARTWORK.CENTURY,
      },
      {
        name: 'Decade',
        code: DATE_FORMAT_ARTWORK.DECADE,
      },
      {
        name: 'Year',
        code: DATE_FORMAT_ARTWORK.YEAR,
      },
      {
        name: 'Month/Year',
        code: DATE_FORMAT_ARTWORK.MONTH_YEAR,
      },
      {
        name: 'Full Date',
        code: DATE_FORMAT_ARTWORK.FULL_DATE,
      },
      {
        name: 'Other',
        code: DATE_FORMAT_ARTWORK.OTHER,
      },
    ];

    this.newArtworkForm.get('isArtistUser')?.valueChanges.subscribe((value) => {
      if (value) {
        this.userName$.subscribe((username) => {
          if (username) {
            this.artists.controls[0].setValue(username);
          }
        });
      } else {
        this.artists.controls[0].setValue('');
      }
    });
  }

  clickFileInput() {
    this.fileToCropper = null;
    this.fileInput.nativeElement.click();
  }

  changeDateFormat(format: number) {
    this.currentDateFormat = format;
    this.date.reset();
  }

  onSubmit() {
    console.log(this.newArtworkForm.controls);
    const formValue = this.newArtworkForm.getRawValue();
    console.log(formValue);
  }

  async onSelectCroppedFile(file: string) {
    this.croppedFile = file;
  }

  addArtist() {
    this.artists.push(this.fb.control(''));
  }

  deleteArtist(artistIndex: number) {
    this.artists.removeAt(artistIndex);
  }

  async onSelectFileUpload(event: Event): Promise<void> {
    const target = event.target as HTMLInputElement;
    const fileList: FileList | null = target.files;
    if (fileList && fileList.length > 0) {
      const file = fileList[0];
      if (!file.type.startsWith('image/')) {
        this.showErrorMessage('ERROR.WRONG_TYPE');
      } else {
        const reader = new FileReader();
        reader.onload = (event) => {
          const img = new Image();
          img.onload = () => {
            if (img.width / img.height > 16 / 9) {
              this.showAspectHorizontalButtons = true;
              this.aspectRatio = 16 / 9;
            } else if (img.width / img.height < 9 / 16) {
              this.showAspectVerticalButtons = true;
              this.aspectRatio = 9 / 16;
            } else {
              this.aspectRatio = img.width / img.height;
            }
          };
          img.src = event.target?.result as string;
        };
        reader.readAsDataURL(file);
        this.fileToCropper = file;
      }
    }
  }

  async storeFile($event: Event) {
    this.isDragover = false;
    this.fileToCropper = ($event as DragEvent).dataTransfer
      ? ($event as DragEvent).dataTransfer?.files.item(0) ?? null
      : ($event.target as HTMLInputElement).files?.item(0) ?? null;
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }

  changeAspectRatio(ratio: number) {
    this.aspectRatio = ratio;
  }

  protected readonly URLS = URLS;
  protected readonly DATE_FORMAT_ARTWORK = DATE_FORMAT_ARTWORK;
}
